html, body {
  font-family: Calibri, sans-serif;
  font-size: 3.2vw;
  height: 100%;
  margin: 0;
  padding: 0;
}

.background-image {
  background-image: url(./images/beach.jpg);
  background-size: cover;
  right: 0;
  opacity: 0.7;
  height: 100%;
  left: 0;
  position: fixed;
  z-index: -1;
}

@media (max-width: 768px) {
  .bio-box {
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    margin: auto;
    width: 80%;
    height: auto;
    background-color: #202020;
    border-radius: 5px;
    padding: 50px 10px 50px 10px;
  }
}

@media (min-width: 769px) {
  .bio-box {
    position: absolute;
    top: 50%;
    left: 50%;
    -ms-transform: translate(-50%, -50%);
    transform: translate(-50%, -50%);
    margin: auto;
    width: 30%;
    height: 50%;
    background-color: #202020;
    border-radius: 5px;
    padding: 50px 10px 55px 10px;
  }
}

.bio {
  color: white;
  text-align: center;
}

#image {
  border-radius: 50%;
  max-width: 35%;
  max-height: 35%;
  content: url(./images/daniel.jpg)
}

#name {
  margin-top: 5%;
  font-size: 15px;
}

#title-master {
  margin-top: 5%;
  font-size: 13px;
}

#title-bachelor {
  font-size: 13px;
  margin-top: 1%;
}

.links-container {
  margin: auto;
  margin-top: 30px;
  width: 50%;
  display: flex;
  justify-content: space-evenly;
}

#link {
  cursor: pointer;
}

.footer {
  position: fixed;
  left: 0;
  bottom: 0;
  width: 100%;
  text-align: center;
  font-size: 30%;
}